.services {
    padding: 60px 0;
    background-color: #f8f8f8;
  }
  
  .container {
    width: 100%;
    margin: 0 auto;
  }
  
  h2 {
    font-size: 36px;
    text-align: center;
    margin-bottom: 50px;
  }
  
  .card {
    background-color: transparent;
    border: none;
    border-radius: 8px;
    transition: all 0.3s ease;
  }
  
  .card-img-top {
    object-fit: cover;
    height: 250px;
    border-radius: 10px;
  }
  
  .card-body {
    padding-top: 20px;
  }
  
  .card-title {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .card-text {
    color: #777;
  }
  
  .btn-primary {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
  }
  